<template>
  <div class="field">
    <b-field
      label="Please enter your address"
      :type="{ 'is-danger': validation.$error }"
      :message="
        validation.$error
          ? 'Please enter your address and pick from the list'
          : ''
      "
    >
      <vue-google-autocomplete
        id="map"
        classname="input"
        autocomplete="off"
        :class="{ 'is-danger': validation.$error }"
        :placeholder="addressPlaceholder"
        :enable-geolocation="true"
        @placechanged="getAddressData"
        @blur="validation.$touch()"
      >
      </vue-google-autocomplete>
    </b-field>

    <div
      v-if="Object.keys(value).length > 0"
      class="has-text-right is-success address-confirmation"
    >
      <b-icon icon="check-circle" type="is-success" size="is-small"> </b-icon>
      {{ formattedAddress }}
    </div>
  </div>
</template>

<script>
import VueGoogleAutocomplete from "vue-google-autocomplete";
export default {
  name: "AddressField",
  components: {
    VueGoogleAutocomplete
  },
  props: {
    value: {
      type: Object,
      required: true
    },
    validation: {
      type: Object,
      required: true
    }
  },
  computed: {
    formattedAddress() {
      if (
        Object.keys(this.value).length > 0 &&
        this.value.constructor === Object
      ) {
        return `${this.value.streetNumber} ${this.value.route}, ${
          this.value.locality
        }, ${this.value.administrativeAreaLevel1}, ${this.value.postalCode}`;
      } else {
        return null;
      }
    },
    addressPlaceholder() {
      if (this.formattedAddress) {
        return this.formattedAddress;
      } else {
        return "123 road suburb city country";
      }
    }
  },
  methods: {
    getAddressData(data) {
      this.$emit("input", {
        streetNumber: data.street_number,
        route: data.route,
        locality: data.locality,
        administrativeAreaLevel1: data.administrative_area_level_1,
        country: data.country,
        postalCode: data.postal_code,
        latitude: data.latitude,
        longitude: data.longitude
      });
    }
  }
};
</script>

<style scoped>
.address-confirmation {
  margin: 1rem 0.2rem 0.7rem 0;
}
</style>
